@charset "UTF-8";
@font-face {
  font-family: "Goudy Mediaeval";
  src: url("../../fonts/Goudy-Mediaeval-Regular.woff2") format("woff2"), url("../../fonts/Goudy-Mediaeval-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Vinque";
  src: url("../../fonts/Vinque.woff2") format("woff2"), url("../../fonts/Vinque.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Noto Serif Simplified Chinese";
  src: url("../../fonts/NotoSerifSC-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Noto Serif Traditional Chinese";
  src: url("../../fonts/NotoSerifTC-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Noto Serif Japanese";
  src: url("../../fonts/NotoSerifJP-Regular.woff2") format("woff2"), url("../../fonts/NotoSerifJP-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Sarabun Thai";
  src: url("../../fonts/SarabunThai-Regular.woff2") format("woff2"), url("../../fonts/SarabunThai-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Noto Serif Thai";
  src: url("../../fonts/NotoSerifThai-Regular.woff2") format("woff2"), url("../../fonts/NotoSerifThai-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Noto Serif Korean";
  src: url("../../fonts/NotoSerifKR-VariableFont_wght.woff2") format("woff2"), url("../../fonts/NotoSerifKR-VariableFont_wght.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
:root {
  --serif-font:
      "Goudy Mediaeval",
      Georgia,
      "Times New Roman",
      Times,
      serif;
  --sans-font:
      Helvetica,
      Arial,
      sans-serif;
  --blue: #679fd0;
  --orange: #d09f67;
  --red: #d06767;
  --dark-orange: #a16930;
  --green: #679f67;
  --dark-purple: #9f679f;
  --purple: #d067d0;
  --grey: #999;
  --good-team: #1d82ff;
  --evil-team: #ff1d1d;
}

:lang(kv),
:lang(pl),
:lang(ru),
:lang(tr),
:lang(uk),
:lang(vi) {
  --serif-font:
      Vinque,
      Georgia,
      "Times New Roman",
      Times,
      serif;
}

:lang(he) {
  --serif-font:
      Georgia,
      "Times New Roman",
      Times,
      serif;
}

:lang(zh-CN) {
  --serif-font:
      Georgia,
      "Times New Roman",
      "Noto Serif Simplified Chinese",
      "FangSong",
      "仿宋",
      STFangSong,
      "华文仿宋",
      serif;
  --sans-font:
      Helvetica,
      Arial,
      "华文细黑",
      "STXihei",
      "PingFang TC",
      "微软雅黑体",
      "Microsoft YaHei New",
      "微软雅黑",
      "Microsoft Yahei",
      "宋体",
      "SimSun",
      sans-serif;
}

:lang(zh-TW) {
  --serif-font:
      Georgia,
      "Times New Roman",
      "Noto Serif Traditional Chinese",
      "FangSong",
      "仿宋",
      STFangSong,
      "华文仿宋",
      serif;
  --sans-font:
      Helvetica,
      Arial,
      "华文细黑",
      "STXihei",
      "PingFang TC",
      "微软雅黑体",
      "Microsoft YaHei New",
      "微软雅黑",
      "Microsoft Yahei",
      "宋体",
      "SimSun",
      sans-serif;
}

:lang(ja) {
  --serif-font:
      Georgia,
      "Times New Roman",
      "Noto Serif Japanese",
      "ＭＳ Ｐ明朝",
      "MS PMincho",
      "Hiragino Mincho Pro",
      "ヒラギノ明朝 Pro W3",
      "游明朝",
      "YuMincho",
      "ＭＳ 明朝",
      "MS Mincho",
      "Hiragino Mincho ProN",
      serif ;
  --sans-font:
      Helvetica,
      Arial,
      "ヒラギノ角ゴ Pro W3",
      "Hiragino Kaku Gothic Pro",
      Osaka,
      メイリオ,
      Meiryo,
      "ＭＳ Ｐゴシック",
      "MS PGothic",
      "ＭＳ ゴシック",
      "MS Gothic",
      "Noto Sans CJK JP",
      TakaoPGothic,
      sans-serif;
}

:lang(th) {
  --serif-font:
      "Noto Serif Thai",
      "Times New Roman",
      Georgia,
      Times,
      serif;
  --sans-font:
      "Sarabun Thai",
      Helvetica,
      Arial,
      sans-serif;
}

:lang(ko) {
  --serif-font:
      "Noto Serif Korean",
      "Times New Roman",
      Georgia,
      Times,
      serif;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
}

html,
body {
  font-size: 1rem;
  line-height: 1.4;
  font-family: var(--sans-font);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff;
  color: #000;
  min-height: 100vh;
}

html {
  -moz-text-size-adjust: none;
  -webkit-text-size-adjust: none;
  text-size-adjust: none;
}

body {
  padding: 1em;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  color: var(--blue);
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
li {
  margin-bottom: 1rem;
}
h1:where(:last-child),
h2:where(:last-child),
h3:where(:last-child),
h4:where(:last-child),
h5:where(:last-child),
h6:where(:last-child),
p:where(:last-child),
li:where(:last-child) {
  margin-bottom: 0;
}

small {
  font-size: 0.85em;
}

[hidden] {
  display: none !important;
}

.token-display, .character-select__characters, .button-list,
.slot {
  --columns: 1;
  --gap: 1em;
  display: flex;
  flex-flow: row wrap;
  gap: var(--gap);
}
.token-display > *, .character-select__characters > *, .button-list > *,
.slot > * {
  margin: 0;
  width: calc(100% / var(--columns) - var(--gap) * (var(--columns) - 1) / var(--columns));
}

.slot--2 {
  --columns: 2;
}

.slot--3 {
  --columns: 3;
}

.slot--4 {
  --columns: 4;
}

.token-display__remove, .character-choice, .character-select__button, .info-token__button, .info-token__hide, .dialog__hide,
.no-btn {
  border: none;
  padding: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  font-weight: inherit;
  color: inherit;
  background-color: transparent;
}

.token-display, .button-list,
.no-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.radio__input,
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
}

.filter-list:not(.is-show-all) > *:not(.is-included) {
  display: none;
}

.body-grimoire {
  background-color: #d0d0d0;
  background-image: url("../../img/background-img.webp");
  background-repeat: repeat;
  background-position: 0 0;
}

.body-sheet {
  background-color: #fffef0;
  background-image: url("../../img/token/character-token-noise.webp");
}

.title {
  font-size: clamp(1.5em, 5vw, 3em);
  font-family: var(--serif-font);
  font-weight: bold;
  letter-spacing: 0.05em;
  text-shadow: -0.05em -0.05em 0.1em rgba(255, 255, 255, 0.8), 0.05em 0.05em 0.1em rgba(0, 0, 0, 0.4);
  margin-bottom: 1rem;
}

.pad {
  border: 0.15em solid #000;
  position: relative;
  background-color: #333;
  background-image: url("../../img/background-img2.webp");
}

.pad--growable {
  height: 80vh;
  resize: vertical;
  overflow: scroll;
}

.pad--bluffs {
  padding: 0.5em;
}

.pad-wrapper {
  position: relative;
  margin-bottom: 1em;
}

.pad-wrapper__icon {
  color: #fff;
  position: absolute;
  right: 0;
  bottom: 0.15em;
  pointer-events: none;
  width: 1em;
  aspect-ratio: 1/1;
}
[dir=rtl] .pad-wrapper__icon {
  right: auto;
  left: 0;
}

.pad__title {
  font-family: var(--serif-font);
  font-weight: normal;
  font-size: 1rem;
  text-shadow: 0.1em 0.1em 0.1em rgba(0, 0, 0, 0.8);
}

@keyframes orphan {
  0% {
    opacity: 1;
    box-shadow: 0 0 15px var(--evil-team);
  }
  100% {
    opacity: 0.8;
    box-shadow: 0 0 0 transparent;
  }
}
.token {
  width: 60px;
  aspect-ratio: 1/1;
  border: none;
  padding: 0;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  transform-origin: 0 0;
}
.token[data-token=character] {
  width: clamp(70px, 16.6666666667vw, 150px);
  transform: scale(var(--token-size, 1));
}
.token[data-token=reminder] {
  width: clamp(55px, 11.1111111111vw, 125px);
  transform: scale(var(--reminder-size, 1));
}
.token[disabled] {
  opacity: 0.7;
}
.token[disabled] > .character {
  --shadow-amount: 0;
}
.token.is-orphan {
  box-shadow: 0 0 15px red;
}
@media (prefers-reduced-motion: no-preference) {
  .token.is-orphan {
    animation: orphan 1s infinite alternate;
  }
}

.token--movable {
  --left: 0;
  --top: 0;
  --z-index: 0;
  cursor: move;
  position: absolute;
  left: calc(var(--left) * 1px);
  top: calc(var(--top) * 1px);
  z-index: var(--z-index);
  touch-action: none;
}

.token--bluff[data-token] {
  transform: none;
}

.character,
.reminder {
  border-radius: 50%;
  display: grid;
  width: 100%;
  aspect-ratio: 1/1;
}

.character {
  --no-leaf: url("data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7");
  --top-leaf: var(--no-leaf);
  --left-leaf: var(--no-leaf);
  --right-leaf: var(--no-leaf);
  --setup-leaf: var(--no-leaf);
  --reminder-offset: 2.5%;
  --shadow-offset: 0.1em;
  --shadow-rotation: 1;
  --shadow-amount: calc(var(--shadow-offset) * var(--shadow-rotation));
  background-color: #fffef0;
  background-image: var(--left-leaf), var(--top-leaf), var(--right-leaf), var(--setup-leaf), url("../../img/token/clockface-2.webp"), url("../../img/token/character-token-noise.webp");
  background-position: 50% 50%;
  background-repeat: no-repeat, no-repeat, no-repeat, no-repeat, no-repeat, repeat;
  background-size: contain, contain, contain, contain, 60%, auto;
  box-shadow: inset 0.1em 0.1em 0.2em rgba(255, 255, 255, 0.2), inset 0 0 0.7em #8a4d0f, var(--shadow-amount) var(--shadow-amount) 0.2em rgba(0, 0, 0, 0.8);
}
.character.is-upside-down {
  --shadow-rotation: -1;
  transform: rotate(180deg);
}
.character.is-dead {
  filter: grayscale(1) brightness(0.8);
}

.character--top-1 {
  --top-leaf: url("../../img/token/top-1.webp");
}

.character--top-2 {
  --top-leaf: url("../../img/token/top-2.webp");
}

.character--top-3 {
  --top-leaf: url("../../img/token/top-3.webp");
}

.character--top-4 {
  --top-leaf: url("../../img/token/top-4.webp");
}

.character--top-5 {
  --top-leaf: url("../../img/token/top-5.webp");
}

.character--top-6 {
  --top-leaf: url("../../img/token/top-6.webp");
}

.character--left-1 {
  --left-leaf: url("../../img/token/left-1.webp");
}

.character--right-1 {
  --right-leaf: url("../../img/token/right-1.webp");
}

.character--right-2 {
  --right-leaf: url("../../img/token/right-2.webp");
}

.character--setup {
  --setup-leaf: url("../../img/token/setup.webp");
}

.character[data-first-night]::before,
.character[data-other-night]::after {
  border-radius: 50%;
  color: #fff;
  font-weight: bold;
  width: 25%;
  aspect-ratio: 1/1;
  display: var(--night-order-display, flex);
  justify-content: center;
  align-items: center;
  align-self: flex-start;
  font-size: clamp(0.75em, 3.5vw - 0.5em, 2em);
  line-height: 1;
  grid-area: 1/-1;
  z-index: 2;
  margin-top: var(--reminder-offset);
}

.character.is-upside-down[data-first-night]::before,
.character.is-upside-down[data-other-night]::after {
  transform: rotate(180deg);
  margin-top: 0;
  margin-bottom: var(--reminder-offset);
  align-self: flex-end;
}

.character[data-first-night]::before {
  content: attr(data-first-night);
  background-image: linear-gradient(to bottom, #006, #00c);
  margin-left: var(--reminder-offset);
  margin-right: auto;
}

.character.is-upside-down[data-first-night]::before {
  margin-left: auto;
  margin-right: var(--reminder-offset);
}

.character[data-other-night]::after {
  content: attr(data-other-night);
  background-image: linear-gradient(to bottom, #600, #c00);
  margin-left: auto;
  margin-right: var(--reminder-offset);
}

.character.is-upside-down[data-other-night]::after {
  margin-left: var(--reminder-offset);
  margin-right: auto;
}

.reminder {
  background-color: #101128;
  box-shadow: inset 0.1em 0.1em 0.2em rgba(255, 255, 255, 0.1), inset 0 0 1em #111324, inset 0 0 0.5em #111324, 0.1em 0.1em 0.2em rgba(0, 0, 0, 0.8);
}

.character__image,
.character__text,
.character__shroud,
.reminder__image,
.reminder__text {
  grid-area: 1/-1;
  z-index: 1;
}

.character__image,
.reminder__image {
  display: flex;
  align-items: center;
  justify-content: center;
}

.character__shroud {
  --background-width: calc(75% * (233 / 419));
  z-index: 2;
  background-image: url("../../img/token/shroud.webp"), linear-gradient(to bottom, #555 57%, transparent 57%);
  background-size: auto 75%, var(--background-width) auto;
  background-repeat: no-repeat;
  background-position: 50% 0;
  filter: drop-shadow(0.1em 0.1em 0.2em rgba(0, 0, 0, 0.8));
  display: none;
}
.is-upside-down > .character__shroud {
  transform: rotate(180deg);
}

.is-dead > .character__shroud {
  display: block;
}

.character__ghost-vote {
  color: #fff;
  width: 30%;
  transform: translateY(100%);
}
.is-voteless .character__ghost-vote {
  display: none;
}

.character__icon {
  width: 90%;
  aspect-ratio: 1/1;
  object-fit: contain;
}

.character__name {
  font-family: var(--serif-font);
  letter-spacing: 0.1em;
}

.character__player-name {
  display: block;
  position: absolute;
  top: 100%;
  width: 100%;
  background-color: #fffef0;
  background-image: url("../../img/token/character-token-noise.webp");
  box-shadow: inset 0.1em 0.1em 0.2em rgba(255, 255, 255, 0.2), inset 0 0 0.7em #8a4d0f, var(--shadow-amount) var(--shadow-amount) 0.2em rgba(0, 0, 0, 0.8);
  font-size: 0.8em;
}
.character__player-name:empty {
  display: none;
}
.is-upside-down > .character__player-name {
  --shadow-amount: var(--shadow-offset);
  top: auto;
  bottom: 100%;
  transform: rotate(180deg);
}

.character__unknown {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 3em;
}
[disabled] .character__unknown {
  display: none;
}

.reminder__text {
  color: #fff;
  font-size: 1.2em;
  letter-spacing: 0.05em;
}

.reminder__icon {
  width: 70%;
}

.details {
  color: #fff;
  background-color: rgba(0, 0, 0, 0.7);
  padding: 0.5em;
  border-radius: 0.5em;
  box-shadow: inset 0.15em 0.15em 0.3em rgba(0, 0, 0, 0.8), 0.1em 0.1em 0.2em rgba(255, 255, 255, 0.8);
  margin-bottom: 1em;
}

.details__summary {
  font-family: var(--serif-font);
  text-shadow: 0.1em 0.1em 0.1em rgba(0, 0, 0, 0.8);
  cursor: pointer;
}

.details__heading {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
  display: inline;
}

.details__body {
  padding-top: 1em;
  padding-bottom: 0.5em;
}

.dialog {
  background-color: transparent;
  border: none;
  margin: auto;
  padding: 0;
}

.dialog--blur-background::backdrop {
  background-image: radial-gradient(circle at center, rgba(0, 0, 0, 0.5) 20%, rgba(0, 0, 0, 0.7) 100%);
  backdrop-filter: blur(0.5em);
}

.dialog--blur-background + .backdrop {
  background-image: radial-gradient(circle at center, rgba(0, 0, 0, 0.5) 20%, rgba(0, 0, 0, 0.7) 100%);
  backdrop-filter: blur(0.5em);
}

.dialog--hide-background::backdrop {
  background-color: #000;
  background-image: url("../../img/background-img3.webp");
}

.dialog--hide-background + .backdrop {
  background-color: #000;
  background-image: url("../../img/background-img3.webp");
}

.dialog__content {
  border: 0.2em solid #000;
  background-color: #fff;
  position: relative;
  width: min(80vw, 30em);
  max-width: 100%;
  background-image: url("../../img/background-img.webp");
  margin-left: auto;
  margin-right: auto;
}
.dialog--polyfilled > .dialog__content {
  max-height: 80vh;
  overflow: auto;
}

.dialog__header {
  padding: 1em;
  display: flex;
  flex-direction: row;
  gap: 1em;
}

.dialog__title {
  font-family: var(--serif-font);
  text-shadow: -0.05em -0.05em 0.1em rgba(255, 255, 255, 0.8), 0.05em 0.05em 0.1em rgba(0, 0, 0, 0.4);
  margin: 0;
  flex-grow: 1;
}

.dialog__hide {
  cursor: pointer;
  font-size: 2em;
  line-height: 1;
  text-shadow: -0.05em -0.05em 0.1em rgba(255, 255, 255, 0.8), 0.05em 0.05em 0.1em rgba(0, 0, 0, 0.4);
}

.dialog__body {
  padding: 1em;
}
.dialog__header + .dialog__body {
  padding-top: 0;
}

.info-token {
  width: min(80%, 40rem);
  text-align: center;
}
.info-token > .dialog__content {
  background-color: #000;
  background-image: none;
  border-style: none;
  display: grid;
}
.info-token > .dialog__content::before {
  content: "";
  grid-area: 1/1;
  background-color: var(--colour, #000);
  opacity: 0.4;
}
.info-token > .dialog__content::after {
  content: "";
  grid-area: 1/1;
  border: 0.1em solid #fff;
  margin: 0.5em;
}
.info-token > .dialog__content > .dialog__body {
  grid-area: 1/1;
  z-index: 1;
}

.info-token__hide {
  cursor: pointer;
  font-size: 2em;
  line-height: 1;
  color: #fff;
  display: block;
  margin-left: auto;
}
[dir=rtl] .info-token__hide {
  margin-left: 0;
  margin-right: auto;
}

.info-token__text {
  font-size: clamp(1rem, 10vw, 3rem);
  margin: 0;
  color: #fff;
}
.info-token__text:lang(de), .info-token__text:lang(id), .info-token__text:lang(ru) {
  font-size: clamp(1rem, 8vw, 3rem);
}
.info-token__text:lang(kv), .info-token__text:lang(pl), .info-token__text:lang(tr) {
  font-size: clamp(1rem, 7vw, 2.5rem);
}
.info-token__text > strong {
  font-family: var(--serif-font);
  font-size: 1.3em;
  letter-spacing: 0.1em;
  display: inline-block;
}

.info-token__actions {
  position: absolute;
  left: 1.2em;
  top: 1.2em;
  display: flex;
  flex-direction: row;
  gap: 1em;
}
[dir=rtl] .info-token__actions {
  left: auto;
  right: 1.2em;
}

.info-token__button {
  color: #fff;
  width: 1em;
}

.info-token-heading {
  font-family: var(--serif-font);
  font-weight: normal;
  font-size: 1em;
  text-shadow: 0.1em 0.1em 0.1em rgba(0, 0, 0, 0.8);
  margin: 1em 0;
}

.carousel {
  display: flex;
  scroll-snap-type: x mandatory;
  overflow-x: scroll;
  scroll-behavior: smooth;
}

.carousel__item {
  width: 100%;
  flex-shrink: 0;
  scroll-snap-align: start;
}

.night-order {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.night-order__item {
  padding: 0.5em 0;
